* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #333945;
  color: #fff;
  height: 100vh;
}
.container {
  width: 100%;
  text-align: center;
}

form {
  margin-top: 230px;
}
form > input {
  margin-right: 20px;
  width: 300px;
  color: #222;
}
form > button {
  padding: 5px 40px;
}
div {
  margin-bottom: 0;
}
ul {
  margin-bottom: 0;
  list-style: none;
  /* border: 1px solid red; */

  text-align: center;
}
ul > li {
  color: #222;
  font-size: 18px;
  margin: 0 auto;
  background-color: #fff;
  width: 500px;
  margin-bottom: 10px;
  padding: 6px;
}
